
'use client';

import { useEffect, useState } from 'react';
import { useAuth } from '@/components/AuthProvider';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';

// Diagnosis record ka type
interface DiagnosisRecord {
  id: string;
  symptoms: string;
  diagnosis: string;
  createdAt: string;
}

export default function HistoryPage() {
  const { user, loading: authLoading } = useAuth();
  const [history, setHistory] = useState<DiagnosisRecord[]>([]);
  const [loading, setLoading] = useState(false); // Default ko false kar dein
  // Ek naya state, server feature ke status ke liye
  const [serverFeatureEnabled, setServerFeatureEnabled] = useState(false);

  useEffect(() => {
    // Yeh check karega ke billing enable hai ya nahi (ek andaza laga kar)
    // Hum seedha isay false set kar rahe hain kyunke hum jante hain billing nahi hai
    const checkServerFeatures = () => {
      // Agar humein pata hai ke billing nahi hai, to isay false set karein
      setServerFeatureEnabled(false);
      setLoading(false);
    };

    if (!authLoading) {
      checkServerFeatures();
    }

  }, [user, authLoading]);

  // Auth loading state
  if (authLoading) {
    return <p className="text-center mt-12">Loading...</p>;
  }

  // User login nahi hai
  if (!user) {
    return <p className="text-center mt-12 text-red-500">Please log in to view your history.</p>;
  }

  return (
    <main className="flex min-h-screen flex-col items-center p-8 bg-gray-900 text-white">
      <div className="w-full max-w-4xl">
        <h1 className="text-3xl font-bold mb-6 text-center text-blue-400">My Diagnosis History</h1>
        
        {/* Agar server feature (billing) enable na ho, to message dikhayein */}
        {!serverFeatureEnabled ? (
          <div className="text-center bg-yellow-900/30 border border-yellow-700 p-6 rounded-lg">
            <h2 className="text-xl font-bold text-yellow-300">Feature Currently Unavailable</h2>
            <p className="text-yellow-400 mt-2">
              The diagnosis history feature is currently disabled because it requires a server connection that is not available.
              This may be due to a missing billing account for the cloud project.
            </p>
            <p className="mt-4">You can still use the AI diagnosis in a limited, local-only mode.</p>
          </div>
        ) : loading ? (
          <p className="text-center mt-12">Loading history...</p>
        ) : history.length === 0 ? (
          <p className="text-center text-gray-400">You have no diagnosis history yet.</p>
        ) : (
          <div className="space-y-4">
            {/* History mapping wala code yahan rahega (abhi ke liye run nahi hoga) */}
          </div>
        )}
      </div>
    </main>
  );
}
